/*  ZoomIn  */
.ZoomIn-appear {
	-webkit-animation: scaleUp .7s ease both;
	animation: scaleUp .7s ease both;
}

.ZoomIn-appear.SwipeToTop-appear-active {
	-webkit-animation: scaleUp .7s ease both;
	animation: scaleUp .7s ease both;

  /* transition: all 0.6s linear; */
}
.ZoomIn-enter {
  -webkit-animation: scaleUp .7s ease both;
	animation: scaleUp .7s ease both;
}
.ZoomIn-enter.ZoomIn-enter-active {
	-webkit-animation: scaleUp .7s ease both;
	animation: scaleUp .7s ease both;
}
.ZoomIn-leave {
  -webkit-animation: scaleUp .7s ease both;
	animation: scaleDown .7s ease both;
}
.ZoomIn-leave.ZoomIn-leave-active {
  -webkit-animation: scaleUp .7s ease both;
	animation: scaleDown .7s ease both;
}





/*  SwipeToTop  */
.SwipeToTop-appear {
    transform:         translateY(300px);
    -webkit-transform: translateY(300px);
    -moz-transform:    translateY(300px);
    -o-transform:      translateY(300px);
    -ms-transform:     translateY(300px);
    opacity: 0;
  }
  
  .SwipeToTop-appear.SwipeToTop-appear-active {
    opacity: 1;
    transform:          translateY(0);
    -webkit-transform:  translateY(0);
    -moz-transform:     translateY(0);
    -o-transform:       translateY(0);
    -ms-transform:      translateY(0);
    transition: all 0.6s linear;
  }
  .SwipeToTop-enter {
    opacity: 0;
    transform:          translateY(530px);
    -webkit-transform:  translateY(530px);
    -moz-transform:     translateY(530px);
    -o-transform:       translateY(530px);
    -ms-transform:      translateY(530px);
  }
  .SwipeToTop-enter.SwipeToTop-enter-active {
    opacity: 1;
    transform:          translateY(0);
    -webkit-transform:  translateY(0);
    -moz-transform:     translateY(0);
    -o-transform:       translateY(0);
    -ms-transform:      translateY(0);
    transition: all 0.2s linear 0.4s;
  }
  .SwipeToTop-leave {
    opacity: 1;
    transform:          translateY(0);
    -webkit-transform:  translateY(0);
    -moz-transform:     translateY(0);
    -o-transform:       translateY(0);
    -ms-transform:      translateY(0);
  }
  .SwipeToTop-leave.SwipeToTop-leave-active {
    opacity: 0;
    position: absolute;
    width: 100%;
    transform:          translateY(-530px);
    -webkit-transform:  translateY(-530px);
    -moz-transform:     translateY(-530px);
    -o-transform:       translateY(-530px);
    -ms-transform:      translateY(-530px);
    transition: all 0.2s linear;
  }
  
  
    /*  SwipeToBottom */
.SwipeToBottom-appear {
  transform:                    translateY(-300px);
  -webkit-transform:            translateY(-300px);
  -moz-transform:               translateY(-300px);
  -o-transform:                 translateY(-300px);
  -ms-transform:                translateY(-300px);
  opacity: 0;
}

.SwipeToBottom-appear.SwipeToBottom-appear-active {
